import React, {useState} from "react";
import "./styles.sass";
import {Button, Typography, IconButton, Box} from "@material-ui/core";
import CheckoutForm from "containers/Checkout/CheckoutForm";
import {KeyboardArrowDown, KeyboardArrowUp} from "@material-ui/icons";


const ProfileForm = ({
  handleSubmit,
  invalid,
  onSubmit,
  initialValues,
  formType,
  ...props
}) => {
  const [open, setOpen] = useState(false)
  const submit = async (value) => {
    await onSubmit({[formType]: value})
  }

  return (
    <div className='ProfileForm'>
      <Box display='flex' justifyContent='space-between'>
        <Typography variant="h6" className="ProfileForm-header">
          {props.title}
        </Typography>
        <IconButton onClick={() => setOpen(!open)}>
          {open ? <KeyboardArrowUp /> : <KeyboardArrowDown />}
        </IconButton>
      </Box>
      <div>
        {open && (
          <form onSubmit={handleSubmit(submit)}>
            <CheckoutForm type={formType} />
            <div className="formBtns">
              <Button
                onClick={() => props.reset()}
                children="Cancel"
              />
              <Button
                variant="contained"
                color="primary"
                type="submit"
                disabled={invalid}
                children="Save"
              />
            </div>
          </form>
        )}
      </div>
    </div>
  )
}

export default ProfileForm
